.App {
  min-height: 100vh;
  background-image: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%);
}
.App >h1{
	text-align: center;
	margin-top: 0px;
	padding-top: 15px;
	font-family: cursive;
}

.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;

}

.search-bar{
	width: 50%;
	border: none;
	padding: 10px;

}

.search-button{
	background: lightcoral;
	border: none;
	padding: 10px 20px;
	color: white;

}

.recipes{

	display: flex;
	justify-content: space-around;
	flex-wrap: wrap;

}